import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import MenuItem from "@mui/material/MenuItem";

const dispatchedEvents = [];

const SrsMuiMenuItem = function (props, ref) {
  return (
    <MenuItem
      disabled={props.disabled}
      selected={props.selected}
      onClick={props.onClick}
      theme={props.muiTheme}
    >
      <slot />
    </MenuItem>
  );
};

const styles = `
  /* Custom styles for the MuiMenuItem component */
`;

const props = [
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "selected",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiMenuItem),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
